// Common styles of the project

html, body {
    height: 100%;
    width: 100%;
    overflow: hidden;
    background: #0e141e;
    font-family: $FontGotham;
}

.loader {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: #0e141e;
    z-index: 30;
    &_img {
        @include position(center);
        @include bg-svg($load);
        animation: 3s togglefaid infinite;
    }
    &.hide {
        opacity: 0;
        visibility: hidden;
        transition: all 0.5s;
    }
}

.start-text {
    @include position(center);
    z-index: 3;
    &:after {
        content: '';
        display: block;
        @include bg-svg($start_text);
        animation: 3s togglefaid infinite;
    }
    &.hide {
        opacity: 0 !important;
        visibility: hidden;
        transition: all 2s;
    }
}

.video-text {
    @include position(centerX);
    bottom: 100px;
    z-index: 3;
    &:after {
        content: '';
        display: block;
        @include bg-svg($video_text);
        animation: 3s togglefaid infinite;
    }
    &.hide {
        opacity: 0 !important;
        visibility: hidden;
        transition: all 2s;

    }
}

@keyframes togglefaid {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }

}

.video-cover {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: #000;
    z-index: 32;
    display: none;
    video {
        min-width: 100%;
        min-height: 100%;
        @include position(center);
    }
    .inversion-video {
        display: none;
    }
}

.webgl {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    transition: all 1s;
    &.active {
        visibility: visible;
        opacity: 1;
        transition: all 0s;
    }
}

.skip {
    position: absolute;
    right: 20px;
    bottom: 20px;
    z-index: 5;
    cursor: pointer;

}

.replay {
    position: absolute;
    left: 20px;
    bottom: 20px;
    z-index: 3;
    cursor: pointer;
    display: none;
    &.active {
        display: block;
    }
    @include respond-to(t) {
        top: auto;
        bottom: 10px;
        left: 10px;
    }
}

.info {
    position: absolute;
    right: 20px;
    top: 20px;
    z-index: 3;
    cursor: pointer;
    display: none;
    &.active {
        display: block;
    }
    @include respond-to(t) {
        top: auto;
        bottom: 10px;
        right: 10px;
    }
}

.original-svg {
    position: relative;
    z-index: 2;
}

.before-svg, .after-svg {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    opacity: 0;

}

.ico_anim {
    opacity: .25;
    @include respond-to(hdd) {
        &:hover {
            opacity: 1;
            .before-svg, .after-svg {
                opacity: 1;
                animation: anim-ico .5s steps(6) infinite;
            }
            .before-svg {
                animation-delay: 0.2s;
            }
        }
    }
}

@keyframes anim-ico {
    0% {
        left: 2px;
        top: 2px;
    }
    20% {
        left: -1px;
        top: -2px;
    }
    40% {
        left: 2px;
        top: -1px;
    }
    60% {
        left: -1px;
        top: 2px;
    }
    80% {
        left: -2px;
        top: -1px;
    }
    100% {
        left: -2px;
        top: -2px;
    }
}

.site-noise {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    pointer-events: none;
    z-index: 31;
    @include respond-to(t) {
        display: none !important;
    }
    .after {

        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        overflow: hidden;
        pointer-events: none;
        z-index: 90;
        background-image: url(../img/content/trame.png);
        background-repeat: repeat-x;
        animation: bg 10s infinite;
        //display: none;
    }
    .before {

        position: absolute;
        height: 100%;
        width: 100%;
        background: url(../img/content/noise.png);
        pointer-events: none;
        display: block;
        animation: grain 2s steps(10) infinite;
        //will-change: transform;
    }

}

@keyframes bg {
    0% {
        background-position: 0 0;
    }
    50% {
        background-position: 1000px 0;
    }
    100% {
        background-position: 0 0;
    }

}

@keyframes grain {
    0% {
        //transform: translate(0)
        background-position: 0 0;
    }
    100% {
        //transform: translate(-10%, 10%);
        background-position: 500px 500px;
    }
    100% {
        //transform: translate(-10%, 10%);
        background-position: -1000px -1000px;
    }
}

.popup {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.75);
    z-index: 100;
    display: none;
    &_content {
        @include position(center);
        width: 560px;
        color: #d7dfe3;
        font-family: $FontGothamLight;
        font-size: 12px;
        text-align: justify;
        line-height: 24px;
        max-width: 90%;
        p {
            margin-bottom: 24px;
        }
    }
    &_bottom {
        text-align: center;
        @include position(centerX);
        color: #d7dfe3;
        bottom: 40px;
        font-size: 10px;
        letter-spacing: 0.2em;
        a {
            color: #d7dfe3;

        }
    }
}

.popup-close {
    cursor: pointer;
    position: absolute;
    top: 40px;
    right: 40px;
    width: 28px;
    height: 28px;
}

.popup-close-line:first-child {
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
    margin-top: 7px;
}

.popup-close-line {
    -webkit-transition: all .4s ease 0s;
    -moz-transition: all .4s ease 0s;
    -ms-transition: all .4s ease 0s;
    -o-transition: all .4s ease 0s;
    transition: all .4s ease 0s;
    height: 1px;
    margin-bottom: 3px;
    background-color: rgba(255, 255, 255, .6);
}

.popup-close-line:last-child {
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
    margin-top: -4px;
}

@include respond-to(hdd) {
    .popup-close:hover .popup-close-line {
        -webkit-transform: rotate(180deg);
        -moz-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        -o-transform: rotate(180deg);
        transform: rotate(180deg);
    }
}

.dg.ac {
    z-index: 999999 !important;
}

audio {
    position: relative;
    z-index: 9999;
    display: none;
}

.black {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #000;
    left: 0;
    top: 0;
    z-index: 999;
    visibility: hidden;
    opacity: 0;
    transition: all 3s;
    display: none;
    &.active {
        visibility: visible;
        opacity: 1;
        transition: all 0s;
    }
}

.loader-image {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
}

.btn {
    position: absolute;
    left: 40px;
    top: 40px;
    z-index: 3;
    font-size: 0;
    display: none;
    @include respond-to(m) {

        left: 50%;
        top: 10px;

        display: none;
        transform: translateX(-50%);
        width: 100%;
        text-align: center;
    }
    &_in {
        width: 153px;
        height: 34px;
        border: 1px solid #fff;
        display: inline-block;
        opacity: .5;
        margin-right: 14px;
        line-height: 34px;
        font-size: 14px;
        text-align: center;
        letter-spacing: 0.4em;
        color: #fff;
        text-transform: uppercase;
        position: relative;
        @include respond-to(m) {
            width: 30%;
            height: 34px;
            margin-right: 5px;
            line-height: 34px;
            font-size: 8px;

        }
        &:before, &:after {
            content: attr(data-text);
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            opacity: 0;
            font-size: 14px;
            text-align: center;
            letter-spacing: 0.4em;

            text-transform: uppercase;
            z-index: -1;
            transform: translate(-2px, -2px);
        }
        &:before {
            border: 2px #4CBD94 solid;
            color: #4CBD94;

        }
        &:after {
            border: 2px #64459C solid;
            color: #64459C;

        }
        @include respond-to(hdd) {
            &:hover {
                opacity: 1;
                &:before, &:after {
                    opacity: 1;
                    animation: anim-ico .5s steps(6) infinite;
                }
                &:before {
                    animation-delay: 0.2s;
                }
            }
        }
    }
}

.texting {
    position: absolute;
    width: 600px;
    max-width: 90%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #ffffff;
    text-shadow: 1px 1px 10px #000000;
    text-align: center;
    font-size: 16px;
    line-height: 20px;
    //visibility: hidden;
    display: none;
    opacity: 1;
    //transition: all 0.5s 0s;
    z-index: 3;
    .progressbar {
        background: none;
    }
    //&.active {
    //    //visibility: visible;
    //    //opacity: 1;
    //    //transition: all 0.5s 1s;
    //
    //}
    .progressbar {
        width: 174px;
        height: 26px;
        margin: auto;
        margin-top: 20px;
        position: relative;
        canvas {
            width: 174px;
            height: 26px;
        }
    }
}

.titles {
    font-family: $FontGotham;
    font-size: 32px;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    margin-bottom: 20px;
    &_after {
        font-family: $FontGothamLight;
        font-size: 20px;
        letter-spacing: 0.15em;
        text-transform: uppercase;

    }
}

.video_gif {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
}

.video_canvas {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}

.start {
    position: absolute;
    bottom: 10%;
    left: 50%;
    transform: translateX(-50%);
    width: 400px;
    transition: all 0.5s;
    animation: blink 3s ease 0s normal none infinite;
    display: none;
    z-index: 2;
    @include respond-to(m) {
        width: 80%;
    }
}

@keyframes blink {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}


.play {
    position: absolute;
    z-index:100000;
    display: none;
}
