$mobile: 750px;
$tablet: 1200px;
$desktop: 1200px;
$desktopH: 1200px;
$desktopHD: 1700px;
@mixin respond-to($media) {
    @if $media == m {
        @media only screen and (max-width: $mobile) {
            @content;
        }
    } @else if $media == t {
        @media only screen and (max-width: $tablet) {
            @content;
        }
    } @else if $media == only-t {
        @media only screen and (max-width: $tablet) and (min-width: $mobile) {
            @content;
        }

    } @else if $media == d {
        @media only screen and (max-width: $desktop) {
            @content;
        }
    } @else if $media == only-d {
        @media only screen and (max-width: $desktop) and (min-width: $tablet) {
            @content;
        }
    } @else if $media == hd {
        @media only screen and (min-width: $desktop) {
            @content;
        }
    } @else if $media == only-hd {
        @media only screen and (min-width: $desktop) and (min-width: $desktopH) {
            @content;
        }
    } @else if $media == hdd {
        @media only screen and (min-width: $desktopH) {
            @content;
        }
    } @else if $media == fhd {
        @media only screen and (min-width: $desktopHD) {
            @content;
        }

    } @else if $media == nomob {
        @media only screen and (min-width: $mobile) {
            @content;
        }
    }

}

/*
@include respond-to(hd) {}
@include respond-to(d) {}
@include respond-to(t) {}
@include respond-to(m) {}
*/

@mixin position($position) {
    @if $position == center {
        position: absolute;
        transform: translate(-50%, -50%);
        left: 50%;
        top: 50%;
    }
    @if $position == centerY {
        position: absolute;
        transform: translateY(-50%);
        top: 50%;
    }
    @if $position == centerX {
        position: absolute;
        transform: translateX(-50%);
        left: 50%;
    }
    @if $position == none {
        position: relative;
        transform: translate(0, 0);
        top: auto;
        left: auto;
    }
}
